import Axios from 'axios'
import Mock from 'mockjs'
import mockData from './mock.js'
import { Message } from 'element-ui'

// 是否mock
const isMock = false

const domain = 'https://szebra.cn'

// 设置mock延迟时间
Mock.setup({
  timeout: '200-400'
})

function msg (msg) {
  if (typeof msg === 'string') return msg
  if (typeof msg === 'object') return JSON.stringify(msg)
  return ''
}

function fetch (type = 'get', ...args) {
  return new Promise(function (resolve) {
    Axios[type](...args)
      .then(res => {
        if (res.data.retcode !== 0) {
          Message({ type: 'error', message: `${res.data.retcode} ${msg(res.data.message)}` })
          resolve(null)
          return
        }
        resolve(res)
      })
      .catch(err => {
        resolve(null)
        Message({ type: 'error', message: `${err.response.data.retcode} ${msg(err.response.data.message)}` })
      })
  })
}
fetch.get = function () { return fetch('get', ...arguments) }
fetch.put = function () { return fetch('put', ...arguments) }
fetch.patch = function () { return fetch('patch', ...arguments) }
fetch.del = function () { return fetch('delete', ...arguments) }
fetch.post = function () { return fetch('post', ...arguments) }

