import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    admin: {
      _id: '',
      name: ''
    },
    isLogin: false
  },
  mutations: {
    // 设置登录状态
    setLogin (state, isLogin = false) {
      state.isLogin = isLogin
    },
    setAdmin (state, admin = {}) {
      state.admin = admin
    }
  },
  actions: {

  }
})
